.scroll-down {
    display: flex;
    justify-content: center;
    &__link {
        display: inline-block;
        vertical-align: top;
        text-decoration: none;
        text-align: center;
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 50px;
        padding: 10px;
    }
    &_layout {
        padding-bottom: 0;
    }
}

@media (min-width: 768px) {
    .scroll-down {
        &__link {
            display: inline-block;
            vertical-align: top;
            text-decoration: none;
            text-align: center;
            border: 1px solid rgba(255, 255, 255, 0.5);
            border-radius: 50px;
            padding: 12px;
        }
        &_layout {
            padding-bottom: 77px;
        }
    }
}
